.carouselContainer {
  margin-top: 100px;
  position: relative;
  height: 700px;

  max-width: 1240px;
  overflow: hidden;
}

.carouselContent {
  position: absolute;
  top: 0;
  left: 0;
  max-width: 1240px;
  z-index: 400;
}

.carouselImage {
  width: 750px;
  height: 400px;
  margin-bottom: 20px;
}

.carouselImage img {
  width: 750px;
  height: 400px;
}

.carouselText,
.carouselItemDetail {
  width: 750px;
  font-family: "Panchang";
  font-weight: 700;
  font-size: 32px;
  line-height: 120%;
  text-transform: uppercase;
  color: #000000;
}

.carouselItemDetail {
  cursor: pointer;
  width: max-content;
  margin-top: 60px;
}

.carouselItemArrow {
  display: inline-block;
  margin-left: 30px;
  width: 36px;
  height: 36px;
  background-size: cover !important;
  background: url("../../assets/image/arrow.svg");
}

.carouselItemDetail:hover .carouselItemArrow {
  background: url("../../assets/image/arrow-hover.svg");
}

@media screen and (max-width: 768px) {
  .carouselContainer {
    height: 420px;
  }

  .carouselContent {
    width: 100%;
  }

  .carouselImage {
    width: 250px;
    height: 130px;
    background-size: cover !important;
  }

  .carouselImage img {
    width: 250px;
    height: 130px;
  }

  .carouselText,
  .carouselItemDetail {
    width: 250px;
    font-size: 16px;
  }

  .carouselItemArrow {
    width: 16px;
    height: 16px;
  }
}
