.advisoryContainer {
  position: relative;
  background-color: white;
  width: 100%;
  height: 900px;

  max-width: 1240px;
  margin: auto;
  padding: 0 10px;
}

.advisoryTitle {
  font-family: "Panchang", sans-serif;
  font-weight: 700;
  font-size: 64px;
  line-height: 120%;
  text-transform: uppercase;
  color: black;
}

.advisoryText {
  margin-top: 30px;
  font-family: "rumiko-clear-demo", sans-serif;
  font-size: 36px;
  line-height: 120%;
  letter-spacing: -0.01em;
  color: black;
}

.listItemText {
  font-family: "Panchang", sans-serif;
  font-weight: 700;
  font-size: 48px;
  line-height: 56px;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  color: #000000;
}

.accelerationIcon,
.techIcon,
.financialIcon {
  width: 60px;
  height: 60px;
  background-size: cover !important;
}

.listItemArrowIcon {
  width: 40px;
  height: 40px;
  background-size: cover !important;

  background: url("../../assets/image/arrow.svg");
}

.advisoryListItemContainer:hover .listItemArrowIcon {
  background: url("../../assets/image/arrow-hover.svg");
}

.accelerationIcon {
  background: url("../../assets/image/advisory/acceleration.png");
}

.financialIcon {
  background: url("../../assets/image/advisory/financialAdvisory.png");
}

.techIcon {
  background: url("../../assets/image/advisory/techAdvisory.png");
}

.advisoryListItemContainer {
  justify-content: space-between;
  padding: 0 16px;
  margin-top: 48px;
  cursor: pointer;
}

.listItemDivider {
  margin-top: 40x;
}

@media screen and (max-width: 768px) {
  .advisoryContainer {
    height: 600px;
    padding: 0 5%;
  }

  .advisoryTitle {
    font-size: 30px;
  }

  .advisoryText {
    font-size: 16px;
  }

  .listItemText {
    height: 20px;
    line-height: 20px;
    font-size: 14px;
  }

  .listItemArrowIcon {
    width: 20px;
    height: 20px;
  }

  .accelerationIcon,
  .techIcon,
  .financialIcon {
    height: 20px;
    width: 20px;
    height: 20px;
  }

  .advisoryListItemContainer {
    height: 30px;
  }
}
