.headerContainer {
  background: #1a1a1a;
}

.headerContent {
  max-width: 1240px;
  margin: 0 auto;
}

.headerFlex {
  width: "90%";
  align-items: center;
  justify-content: space-between;
  color: white;
  height: 105px;

  font-family: "Rumiko Clear Demo", sans-serif;
  font-size: 16px;
  color: #ffffff;

  margin-bottom: -1px;
}

.headerLogo {
  width: 140px;
  height: 40px;
  background: url("../../assets/image/logo.svg");
  background-repeat: no-repeat;
}

.headerLink {
  margin-right: 30px;

  font-family: "Rumiko Clear Demo", sans-serif;
  font-size: 16px;
  line-height: 21px;
}

@media screen and (max-width: 768px) {
  .headerLinkContainer {
    display: none;
    /*padding-left: 30px;*/
  }
  .headerLogo{
    margin-left: 20px;
  }

  .headerFlex {
    /*justify-content: center;*/
  }
}
