.partnerContainer {
  position: relative;
  background-color: white;
  width: 100%;
  height: 3200px;
  margin-top: 100px;
}

.partnerBg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2000px;
  z-index: 100;
}

.partnerContent {
  position: absolute;
  top: 500px;
  left: 0;
  width: 100%;
  height: 3000px;
  color: white;
  z-index: 200;
  padding: 0 10px;
  max-width: 1240px;
  margin-left: 50vw;
  transform: translateX(-50%);
}

.partnerSlogan {
  font-family: "Panchang";
  font-weight: 700;
  font-size: 64px;
  line-height: 120%;
  text-transform: uppercase;
}

.partnerMap {
  width: 100%;
  height: 450px;
  margin-top: 150px;
  margin-bottom: 100px;
  background: url("../../assets/image/partner/map.png");
}

.partnerText {
  display: inline-block;
  margin-top: 30px;
  font-family: "rumiko-clear-demo", sans-serif;
  font-size: 24px;
  line-height: 32px;
  width: 50%;
}

.partnerCity {
  margin-bottom: 200px;
  display: inline-block;
  font-family: "Panchang";
  font-weight: 700;
  font-size: 24px;
  line-height: 120%;
}

.partnerBrain {
  margin: auto;
  max-width: 1280px;
  height: 640px;
  margin-top: 100px;
  background-size: cover !important;
  background: url("../../assets/image/partner/brain.png");
}

@media screen and (max-width: 768px) {
  .partnerContainer {
    height: 1000px;
  }

  .partnerContent {
    padding: 0 5%;
    top: 90px;
  }

  .partnerSlogan {
    font-size: 30px;
  }

  .partnerMap {
    margin: 100px 0 50px 0 !important;
    height: 120px;
    background-size: contain;
    background-repeat: no-repeat;
  }

  .partnerText {
    font-size: 16px;
    line-height: 24px;
    width: 100%;
  }

  .partnerCity {
    display: none;
  }

  .partnerBrain {
    height: 320px;
    border-radius: 20px;
    background-size: contain !important;
    background-repeat: no-repeat !important;
  }
}
