.bannerContainer {
  position: relative;
  background-color: #1a1a1a;
  width: 100%;
  height: 1000px;
  padding: 80px 5%;
}
.bannerContent {
  position: relative;
  max-width: 1240px;
  margin: auto;
}

.bannerBg1,
.bannerBg2 {
  position: absolute;
  top: 300px;
  left: 0;
  width: 100%;
  background-size: cover !important;
  background-repeat: repeat-x !important;
}

.bannerBg1 {
  height: 800px;
  background: url("../../assets/image/banner/bg1.svg");
  z-index: 100;
}
.bannerBg2 {
  height: 400px;
  background: url("../../assets/image/banner/bg2.svg");
  z-index: 200;
}

.bannerBadge {
  position: absolute;
  bottom: -500px;
  right: 0;
  width: 180px;
  height: 180px;
  background-size: cover !important;
  background-repeat: no-repeat;

  background: url("../../assets/image/banner/badge.png");
  z-index: 100;
}

.bannerSlogan {
  font-family: "Panchang";
  font-weight: 700;
  font-size: 80px;
  line-height: 96px;
  text-transform: uppercase;
  color: white;
}

.bannerText {
  margin-top: 30px;
  font-family: "rumiko-clear-demo", sans-serif;
  font-size: 24px;
  line-height: 32px;
  color: #ffffff;
}

@media screen and (max-width: 768px) {
  .bannerSlogan {
    font-size: 30px;
    line-height: 56px;
  }

  .bannerText {
    font-size: 16px;
    line-height: 24px;
  }
}
