.meetRootzContainer {
  position: relative;
  background: linear-gradient(to bottom, white, white 50%, #131313 55%);
  width: 100%;
  height: 1400px;
  margin-top: 100px;
}

.meetRootzBg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2000px;
  z-index: 100;
}

.meetRootzContent {
  position: absolute;
  top: 0px;
  left: 0;
  width: 100%;
  height: 3000px;
  color: white;
  z-index: 200;

  max-width: 1240px;
  margin-left: 50%;
  transform: translateX(-50%);
}

.meetRootzSlogan {
  font-family: "Panchang";
  font-weight: 700;
  font-size: 64px;
  line-height: 120%;
  text-transform: uppercase;
  color: black;
}

.meetRootzMeeting {
  width: 100%;
  height: 700px;
  margin-top: 150px;
  margin-bottom: 100px;
  border-radius: 20px;
  background: url("../../assets/image/meetRootz/meeting.png");
  background-size: cover !important;
  background-repeat: no-repeat !important;
  background-color: #a1a1a1;
}

.meetRootzFlex {
  justify-content: space-between;
  align-items: center;
}

.meetRootzText {
  display: inline-block;
  margin-top: 30px;
  font-family: "rumiko-clear-demo", sans-serif;
  font-size: 24px;
  line-height: 32px;
  width: 50%;
}

.meetRootzGlimpse {
  font-family: "Rumiko Clear Demo", sans-serif;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: 0.05em;
  text-decoration: underline;
  text-underline-offset: 10px;
  cursor: pointer;
  width: 20%;
}

@media screen and (max-width: 768px) {
  .meetRootzContainer {
    height: 800px;
  }

  .meetRootzBg {
    top: 100px;
    height: 1500px;
  }

  .meetRootzContent {
    padding: 0 5%;
    height: 2000px;
  }

  .meetRootzSlogan {
    font-size: 30px;
  }

  .meetRootzFlex {
    flex-direction: column;
    gap: 40px;
  }

  .meetRootzMeeting {
    margin: 50px auto;
    width: 350px;
    height: 200px;
  }

  .meetRootzText {
    font-size: 18px;
    width: 100%;
  }

  .meetRootzGlimpse {
    width: 100%;
  }
}
