.portfolioContainer {
  position: relative;
  background-color: white;
  width: 100%;
  min-height: 1900px;
}
.portfolioInnerContainer {
  position: absolute;
  width: 100%;
  z-index: 300;

  max-width: 1240px;
  margin-left: 50%;
  transform: translateX(-50%);
  padding: 0 10px;
}

.portfolioSlogan {
  font-family: "Panchang";
  font-weight: 700;
  font-size: 64px;
  line-height: 120%;
  text-transform: uppercase;
  color: black;
}

.portfolioText {
  margin-top: 30px;
  font-family: "rumiko-clear-demo", sans-serif;
  font-size: 24px;
  line-height: 32px;
  color: black;
}

.portfolioListItemContainer {
  width: 300px;
  height: 200px;
  border: 1px solid #dddddd;
  position: relative;
}

.portfolioListItemContainer img {
  margin: 100px auto 0 auto;
  transform: translateY(-50%);
  padding: 0 10px;
}

.portfolioListItemText {
  position: absolute;
  bottom: 10px;
  width: 300px;
  margin-left: 150px;
  transform: translateX(-50%);

  font-family: "Panchang", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 120%;

  text-transform: uppercase;
  text-align: center;

  color: #666666;
}

.portfolioFlex {
  justify-content: center;
  gap: 10px;
  margin-top: 150px;
  flex-wrap: wrap;
}

@media screen and (max-width: 768px) {
  .portfolioContainer {
    min-height: 1600px;
  }
  .portfolioInnerContainer {
    padding: 0 5%;
  }

  .portfolioSlogan {
    font-size: 30px;
  }

  .portfolioText {
    font-size: 16px;
  }

  .portfolioListItemContainer {
    width: 150px;
    height: 100px;
    border: 1px solid #dddddd;
    position: relative;
  }

  .portfolioListItemContainer img {
    margin-top: 50px;
    width: 40px;
  }

  .portfolioListItemText {
    font-size: 8px;
    margin-left: 75px;
  }

  .portfolioFlex {
    margin-top: 70px;
  }
}
