.subscriptionContainer {
  position: relative;
  background-color: #131313;
  width: 100%;
  height: 600px;
}

.subscriptionContent {
  background: url("../../assets/image/subscription/bg.png");
  background-size: contain !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 600px;
  color: black;
  z-index: 200;

  max-width: 1240px;
  margin-left: 50%;
  transform: translateX(-50%);
}

.subscriptionSlogan {
  font-family: "Panchang";
  font-weight: 700;
  font-size: 48px;
  line-height: 100%;
  text-transform: uppercase;
  color: white;
}

.subscriptionText {
  margin-top: 20px;
  font-family: "Rumiko Clear Demo";
  font-size: 24px;
  line-height: 140%;
  letter-spacing: -0.01em;
  color: #ffffff;
  opacity: 0.5;
}

.subscriptionLink {
  display: inline-block !important;
  margin-right: 20px;
  margin-bottom: 20px;

  font-family: "Rumiko Clear Demo";
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;

  color: #ffffff;
}

.subscriptionInputRightElement {
  cursor: pointer;
  margin-top: 20px;
  margin-right: 20px;
}

.subscriptionlistItemArrowIcon {
  display: inline-block !important;
  width: 16px;
  height: 16px;
  background-size: cover !important;

  background: url("../../assets/image/arrow.svg");
}

.subscriptionlistItemArrowIcon:hover {
  background: url("../../assets/image/arrow-hover.svg");
}

.footerFlex {
  margin-top: 60px;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  color: white;
  height: 105px;
}

.footerLogo {
  width: 220px;
  height: 60px;
  background: url("../../assets/image/logo.svg");
  background-size: cover !important;
  background-repeat: no-repeat !important;
}

.footerYoutube,
.footerTwitter,
.footerMedium,
.footerLinkedin,
.footerApplePodcast,
.footerSpotifyEpisode
{
  cursor: pointer;
  display: inline-block;
  margin: 0 10px;
  width: 30px;
  height: 20px;
  background-size: contain !important;
  background-repeat: no-repeat !important;
}

.footerInstagram {
  background: url("../../assets/image/subscription/instagram.png");
}

.footerMedium {
  background: url("../../assets/image/subscription/medium.png");
}

.footerTwitter {
  background: url("../../assets/image/subscription/twitter.png");
}

.footerYoutube {
  background: url("../../assets/image/subscription/youtube.png");
}

.footerLinkedin {
  background: url("../../assets/image/subscription/linkedin.svg");
}

.footerApplePodcast {
  background: url("../../assets/image/subscription/podcast.svg");
}

.footerSpotifyEpisode {
  background: url("../../assets/image/subscription/spotify.svg");
}

.footerText {
  font-family: "Montserrat";
  font-size: 12px;
  opacity: 0.5;
  text-align: center;
}

.subscriptionFlex {
  justify-content: space-between;
  margin-top: 100px;
}

@media screen and (max-width: 768px) {
  .subscriptionContainer {
    height: 800px;
  }

  .subscriptionFlex {
    flex-direction: column;
    gap: 50px;
    margin-top: 50px;
  }

  .subscriptionContent {
    height: 100%;
    padding: 0 5%;
  }

  .subscriptionSlogan {
    font-size: 30px;
  }

  .subscriptionText {
    font-size: 16px;
  }

  .subscriptionLink {
    font-size: 16px;
  }

  .subscriptionInputRightElement {
    margin-right: 10px;
  }

  .footerFlex {
    margin-top: 20px;
    flex-direction: column-reverse;
  }

  .footerLogo {
    width: 110px;
    height: 30px;
  }

  .footerYoutube,
  .footerTwitter,
  .footerMedium,
  .footerInstagram {
    width: 20px;
    height: 15px;
  }

  .footerText {
    font-size: 10px;
  }

  .footerSpacer {
    display: none;
  }
}
